blink,
.blink {
  -webkit-animation: blink 1s step-end infinite;
  animation: blink 1s step-end infinite;
}

@-webkit-keyframes blink {
  67% {
    opacity: 0;
  }
}

@keyframes blink {
  67% {
    opacity: 0;
  }
}

.bounce-me {
  -webkit-animation: bounce 1s infinite;
  animation: bounce 1s infinite;
}

@-webkit-keyframes bounce {
  60% {
    -webkit-transform: translate(0px, -10px);
            transform: translate(0px, -10px);
  }
}

@keyframes bounce {
  60% {
    -webkit-transform: translate(0px, -10px);
            transform: translate(0px, -10px);
  }
}

@-webkit-keyframes marquee-scroll-left {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }

  100% {
    -webkit-transform: translate(-100%, 0);
            transform: translate(-100%, 0);
  }
}

@keyframes marquee-scroll-left {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }

  100% {
    -webkit-transform: translate(-100%, 0);
            transform: translate(-100%, 0);
  }
}

@-webkit-keyframes marquee-scroll-right {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }

  100% {
    -webkit-transform: translate(100%, 0);
            transform: translate(100%, 0);
  }
}

@keyframes marquee-scroll-right {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }

  100% {
    -webkit-transform: translate(100%, 0);
            transform: translate(100%, 0);
  }
}

@-webkit-keyframes marquee-bounce-left {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }

  100% {
    -webkit-transform: translate(-90%, 0);
            transform: translate(-90%, 0);
  }
}

@keyframes marquee-bounce-left {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }

  100% {
    -webkit-transform: translate(-90%, 0);
            transform: translate(-90%, 0);
  }
}

.marquee-bounce-right {
  width: 100%;
  margin: 0 auto;
  white-space: nowrap;
  overflow: hidden;
  box-sizing: border-box;
}

.marquee-bounce-right > * {
  display: inline-block;
  padding-right: 100%;
  text-indent: 0;
  -webkit-animation: marquee-bounce-right 15s linear infinite alternate;
          animation: marquee-bounce-right 15s linear infinite alternate;
}

@-webkit-keyframes marquee-bounce-right {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }

  100% {
    -webkit-transform: translate(90%, 0);
            transform: translate(90%, 0);
  }
}

@keyframes marquee-bounce-right {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }

  100% {
    -webkit-transform: translate(90%, 0);
            transform: translate(90%, 0);
  }
}


